#form {
    width: 40%;
    padding: 25px;
    border: 1px solid gray;
    border-radius: 5px;
}

#general-info-section,
.education-div,
.work-div {
    display: flex;
    flex-direction: column;
}

#form h3:first-child {
    margin-top: 20px;
}

#form h3 {
    margin-bottom: 7px;
}

#form input {
    margin-bottom: 10px;
    padding: 5px;
    font-size: 0.9rem;
    border: 1px solid gray;
    border-radius: 3px;
}

#form span {
    border: 1px solid gray;
    border-radius: 3px;
    margin-bottom: 10px;
    padding: 5px;
    font-size: 0.9rem;
    line-height: 1.3;
}

#form span:empty::before {
    content: attr(data-placeholder);
    display: block;
    height: 100%;
    color: #00000090;
}

#form input,
#form span {
    color: #535353;
}

#form input:focus,
#form span:focus {
    outline-color: #62a4a4;
}

#form button {
    margin: 0 auto 25px 0;
    padding: 5px 10px;
    background-color: white;
    color: #535353;
    border: 1px solid gray;
    border-radius: 3px;
    cursor: pointer;
}

#form button:hover {
    border: 1px solid #407070;
    background-color: #62a4a4;
    color: white;
    transition: border 0.2s, background-color 0.2s, color 0.2s;
}